<template>
  <div>
    <v-row v-if="$vuetify.breakpoint.mdAndDown" no-gutters justify="end">
      <v-col class="center-align" cols="12">
        <v-btn class="inbox-tab-mobile" :color="selectedTab == 'inbox' ? 'primary' : ''" depressed @click="onChangeTab('inbox')">{{ $t(`button.inbox`) }}</v-btn>
        <v-btn class="inbox-tab-mobile" :color="selectedTab == 'announcement' ? 'primary' : ''" depressed @click="onChangeTab('announcement')">{{ $t(`button.notification`) }}</v-btn>
      </v-col>
    </v-row>
    <v-card class="desktop_card" elevation="0">
      <v-card-title class="justify-space-between" style="padding-left: 0px; padding-bottom: 10px;">
        <label class="--v-primary-base text-left">{{ $t(`label.inbox`) }}</label>
        <v-btn v-if="$vuetify.breakpoint.mdAndDown" icon elevation="0" class="icon-btn mt=0 mr-2" @click="deleteMsg()">
          <delete-icon></delete-icon>
        </v-btn>
      </v-card-title>
      <v-row v-if="$vuetify.breakpoint.lgAndUp" no-gutters justify="end" class="px-10">
        <v-col class="text-right" cols="12">
          <v-btn icon elevation="0" class="icon-btn mr-2" @click="deleteMsg()">
            <delete-icon></delete-icon>
          </v-btn>
          <v-btn class="inbox-tab" :color="selectedTab == 'inbox' ? 'primary' : ''" depressed @click="onChangeTab('inbox')">{{ $t(`button.inbox`) }}</v-btn>
          <v-btn class="inbox-tab" :color="selectedTab == 'announcement' ? 'primary' : ''" depressed @click="onChangeTab('announcement')">{{ $t(`button.notification`) }}</v-btn>
        </v-col>
      </v-row>
      <v-card style="border-radius: 0px !important;" class="profile-detail-card inbox-card" elevation="0">
        <v-row v-if="selectedTab == 'announcement'" no-gutters justify="start">
          <v-col cols="12" v-for="(a, index) in selectedList" :key="a.id" :class="a.has_read ? 'inbox-message-isread' : ''">
            <v-row no-gutters class="py-1">
              <v-col cols="12" md="3" lg="2">
                <v-checkbox class="mt-0" v-bind:label="a.send_at | shortDate" color="primary" v-model="a.selected" hide-details></v-checkbox>
              </v-col>
              <v-col cols="12" md="9" lg="10">
                <v-row no-gutters :class="$vuetify.breakpoint.mdAndDown ? 'ml-8' : ''" @click="readInboxMsg(a.id)">
                  <v-col cols="12" :class="$vuetify.breakpoint.mdAndDown ? 'my-3' : 'mb-3'">
                    <label class="font-weight-bold subtitle-1">{{ a.subject }}</label>
                  </v-col>
                  <v-col cols="12">
                    <label>{{ a.message }}</label>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <v-row no-gutters class="py-3">
              <v-col cols="12">
                <v-divider v-if="index !== announcementList.length - 1"></v-divider>
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="12" class="pt-1 pb-3 text-center" v-if="announcementList.length == 0">
            <label class="field-name">{{ $t(`message.noMeassage`) }}</label>
          </v-col>
          <!--            <v-col cols="12">-->
          <!--            <v-divider></v-divider>-->
          <!--            </v-col>-->
        </v-row>
        <v-row v-if="selectedTab == 'inbox'" no-gutters align="start" justify="start">
          <v-col cols="12" v-for="(a, index) in selectedList" style="padding-bottom: 10px; padding-top: 10px;" :key="a.id" :class="a.has_read ? 'inbox-message-isread' : ''">
            <v-row no-gutters class="py-1 inboxboxes">
              <v-col cols="12" md="3" lg="2">
                <v-checkbox v-model="a.selected" class="mt-0" v-bind:label="a.send_at | shortDate" color="primary" hide-details></v-checkbox>
              </v-col>
              <v-col cols="12" md="9" lg="10" @click="openMsgDialog(a)">
                <v-row no-gutters :class="$vuetify.breakpoint.mdAndDown ? 'ml-8' : ''">
                  <v-col cols="12" :class="$vuetify.breakpoint.mdAndDown ? 'my-3' : 'mb-3'">
                    <label class="font-weight-bold subtitle-1">{{ a.subjects && a.subjects[language] ? a.subjects[language] : a.subject }}</label>
                  </v-col>
                  <v-col cols="12">
                    <label>{{ a.message }}</label>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <!--            <v-row no-gutters class="py-3">-->
            <!--            <v-col cols="12">-->
            <!--            <v-divider v-if="index !== inboxList.length -1"></v-divider>-->
            <!--            </v-col>-->
            <!--            </v-row>-->
          </v-col>
          <v-col cols="12" class="pt-1 pb-3 text-center" v-if="inboxList.length == 0">
            <label class="field-name">{{ $t(`message.noMeassage`) }}</label>
          </v-col>
          <!--            <v-col cols="12">-->
          <!--            <v-divider></v-divider>-->
          <!--            </v-col>-->
        </v-row>
        <v-row no-gutters class="mb-6" v-if="pagination.totalPage > 1">
          <v-col cols="12">
            <v-pagination
              light
              circle
              class="vip_summary_pagination"
              v-model="pagination.currentPage"
              :length="pagination.totalPage"
              @input="changePage"
              :next-icon="'mdi-chevron-right'"
              :prev-icon="'mdi-chevron-left'"
              :total-visible="pagination.paginationPageLimit"
            ></v-pagination>
          </v-col>
        </v-row>
      </v-card>
    </v-card>
    <app-dialog :dialogShow="msgDialog.show" :max-width="1100" :title="msgDialog.title" :subtitle="msgDialog.subtitle" :closeAction="this.closeMsgDialog">
      <v-container class="maxFrameHeight">
        <div v-html="msgDialog.msg" :class="['inbox_msg_content', $vuetify.breakpoint.mdAndUp ? 'flow-overHeight' : '']"></div>
      </v-container>
    </app-dialog>
  </div>
</template>
<script>
import { uiHelper, locale, formValidator } from '@/util'
import { SHARED } from '@/constants/constants'
import deleteIcon from '@/assets/svg/delete-icon.svg'
import cloneDeep from 'lodash/cloneDeep'
import map from 'lodash/map'
import { INBOX_GET_MSG, INBOX_READ_MSG, INBOX_DELETE_MSG } from '@/store/inbox.module'
import { INBOX_GET_MSG_CONTENT } from '../../store/inbox.module'

export default {
  name: 'myInbox',
  components: {
    deleteIcon
  },
  data: () => ({
    language: uiHelper.getLanguage(),
    validator: formValidator,
    shared: SHARED,
    selectedTab: 'inbox',
    msgDialog: {
      show: false
    },
    inboxList: [],
    announcementList: [],
    selectedList: [],
    pagination: {
      currentPage: 1,
      totalPage: 1,
      paginationPageLimit: 10
    },
    searchCriteria: {
      pageNumber: 1,
      pageSize: 20,
      mobilePageSize: 10
    }
  }),
  created() {
    this.metaTag()
    this.getInboxMsg()
  },
  computed: {
    msgRes() {
      return this.$store.state.inbox.inboxMsg
    }
  },
  watch: {
    msgRes() {
      this.getFilterAnnoucementList()
      this.getFilterInboxList()
      this.onChangeTab(this.selectedTab)
    }
  },
  methods: {
    metaTag() {
      const currentUrl = window.location.href
      uiHelper.setCanonical(currentUrl)
      const domainWithProtocol = currentUrl.split('/')[2]
      const domain = domainWithProtocol.replace(/(^\w+:|^)\/\//, '')
      const path = window.location.pathname
      const cleanPath = path.replace(/\/(en-BD|bn-BD)/i, '')
      const Links = `https://${domain}${cleanPath}`
      const Linksen = `https://${domain}/en-BD${cleanPath}`
      const Linksbn = `https://${domain}/bn-BD${cleanPath}`
      uiHelper.setMetaTag('url', Links)
      uiHelper.setMetaTag('og:url', Links)
      uiHelper.setHreflangLinks(Links)
      uiHelper.setHreflangLinksen(Linksen)
      uiHelper.setHreflangLinksbd(Linksbn)
    },
    getFilterAnnoucementList() {
      let self = this
      this.announcementList = cloneDeep(
        this.$store.state.inbox.inboxMsg.filter(a => {
          self.$options.filters.shortDate(a.sent_at)
          return a.category == '2'
        })
      )
    },
    getFilterInboxList() {
      let self = this
      this.inboxList = cloneDeep(
        this.$store.state.inbox.inboxMsg.filter(a => {
          self.$options.filters.shortDate(a.sent_at)
          return a.category == '1' || a.category == '5'
        })
      )
    },
    getInboxMsg() {
      this.$store.dispatch(`${INBOX_GET_MSG}`)
    },
    readInboxMsg(msgId, categoryId) {
      let obj = {
        msgId: msgId,
        categoryId: categoryId
      }
      this.$store.dispatch(`${INBOX_READ_MSG}`, { obj })
      this.getInboxMsg()
    },
    async getInboxMsgContent(msg) {
      let obj = {
        msgId: msg.id,
        category: msg.category,
        language: uiHelper.getLanguage()
      }
      await this.$store.dispatch(`${INBOX_GET_MSG_CONTENT}`, { obj })
    },
    async openMsgDialog(selectedMsg) {
      await this.getInboxMsgContent(selectedMsg)
      let inboxContent = this.$store.state.inbox.inboxMsgContent
      this.msgDialog = {
        show: true,
        msg: inboxContent.content,
        title: inboxContent.subject,
        subtitle: inboxContent.message
      }
      this.readInboxMsg(selectedMsg.id, selectedMsg.category)
    },
    closeMsgDialog() {
      this.msgDialog = {
        show: false
      }
    },
    onChangeTab(tab) {
      this.selectedTab = tab
      if (tab == 'inbox') {
        this.renderPage(this.inboxList)
      } else {
        this.renderPage(this.announcementList)
      }
    },
    renderPage(list) {
      if (this.$vuetify.breakpoint.xsOnly) {
        this.pagination.totalPage = Math.ceil(list.length / this.searchCriteria.mobilePageSize)
      } else {
        this.pagination.totalPage = Math.ceil(list.length / this.searchCriteria.pageSize)
      }
      this.searchCriteria.pageNumber = 1
      this.changePage(1)
    },
    changePage(targetPage) {
      let list = []
      if (this.selectedTab == 'inbox') {
        list = this.inboxList
      } else {
        list = this.announcementList
      }
      this.searchCriteria.pageNumber = targetPage
      if (this.$vuetify.breakpoint.xsOnly) {
        this.selectedList = list.slice((this.searchCriteria.pageNumber - 1) * this.searchCriteria.mobilePageSize, this.searchCriteria.pageNumber * this.searchCriteria.mobilePageSize)
      } else {
        this.selectedList = list.slice((this.searchCriteria.pageNumber - 1) * this.searchCriteria.pageSize, this.searchCriteria.pageNumber * this.searchCriteria.pageSize)
      }
    },
    async deleteMsg() {
      let list = this.selectedList
        .filter(a => a.selected)
        .map(a => ({
          id: a.id,
          category: a.category
        }))

      if (list.length > 0) {
        await this.$store.dispatch(`${INBOX_DELETE_MSG}`, { payload: list })
        this.getInboxMsg()
      } else {
        this.$parent.openAlert(false, locale.getMessage(`message.deleteMsgError`))
      }
    }
  }
}
</script>
