  .mobile_card {
    margin-top: 0;
    border-radius: 20px
  }

  .desktop_card {
    border-radius: 20px
  }
  .v-data-table-header, .v-data-table__wrapper table .v-data-table-header tr{
    background-color: #2c3482 !important;
  }
  .announcement-table {
    background-color: transparent!important;
  }

  .announcement-table tr:nth-child(odd) {
    background-color: #1d223d!important;
  }

  .announcement-table tr:nth-child(even) {
    background-color: #2d3454!important;
  }

  .announcement-page {
  min-height: 650px;
}

